  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.contenedor-residencial h1 {
  font-size: 4rem !important;
  font-weight: 900;
  color: 900;
}
.contenedor-residencial h2 {
  text-align: left;
}

.resumen-residencial {
  margin: 0 5rem 0 5rem;
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 480px) {
  .resumen-residencial {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-evenly;
  }
}
@media (min-width: 768px) {
  .resumen-residencial {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-evenly;
  }
}

@media (min-width: 480px) {
  .contenido-modelos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 480px) and (min-width: 768px) {
  .contenido-modelos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.contenido-modelo {
  display: flex;
  flex-direction: column;
}
.contenido-modelo h2 {
  text-align: center;
}

.resumen-modelo {
  flex: 1;
  background-color: #FFFFFF;
  border: 3px solid #E08709;
  border-top-color: #FFFFFF;
  padding: 3rem;
  display: flex;
  flex-direction: column;
}
.resumen-modelo .precio {
  color: #71B100;
}
.resumen-modelo ul:nth-child(1), .resumen-modelo ul:nth-child(2) {
  max-width: none;
}
.resumen-modelo ul:nth-child(1) li, .resumen-modelo ul:nth-child(2) li {
  display: flex;
  justify-content: center;
  align-items: center;
}
.resumen-modelo ul:nth-child(2) img {
  width: 4rem;
}
.resumen-modelo .descripcion {
  margin: -3rem 0;
  display: block;
  list-style: square;
}
.resumen-modelo .descripcion p {
  word-break: keep-all;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 10rem;
}